.images, .img-responsive {
    width: 20%;
    float: left;
    padding: 5px;
    margin: 15px;
    /* from w3 schools on box shadow */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.cardHolder{
    width: 100%;
    height: auto;
    margin: 0 auto;
    overflow: auto;
 } 

 .imgHolder {
    width: 80%;
    margin: auto;
 }


 /*below from w3 schools*/
 /* Float to the right on screens that are equal to or less than 768px wide */
@media (max-width: 768px) {
    .float-right-sm {
      float: right;
    }
  }
  
  /* Float to the right on screens that are equal to or greater than 769px wide */
  @media (min-width: 769px) {
    .float-right-lg {
      float: right;
    }
  }